<template>
    <v-card  class="text-center">
        <br>
        <h3>ແກ້ໄຂໂປຣຟຣາຍ</h3>
        <v-avatar width="120" height="120">
            <v-img style="border-radius: 120px"
                   :src="base64 ? base64 : (  auth.image ? $server + auth.image
                       :'https://www.w3schools.com/howto/img_avatar.png' ) "></v-img>
        </v-avatar>
        <v-flex
                xs10 sm10 md8 lg8
                class="mx-auto"
        >
          <div>
          </div>
            <v-form @submit.prevent="updateProfile">
                <div class="file-input">
                    <label class="v-btn info" for="file">
                        <i class="fas fa-upload fa-lg mr-2"></i>
                        ເລືອກຮູບໂປຣຟຣາຍ</label>
                    <input @change="imgUpload" type="file" id="file">
                </div>
                <v-text-field
                        v-model="auth.username"
                        label="username"
                        outlined
                        disabled
                ></v-text-field>
                <v-text-field
                        v-model="auth.name"
                        label="ຊື່"
                        outlined
                ></v-text-field>
                <v-text-field
                        v-model="auth.lastname"
                        label="ນາມສະກຸນ"
                        outlined
                ></v-text-field>
                <v-text-field
                        v-model="auth.email"
                        label="ອີເມວ"
                        disabled
                        outlined
                ></v-text-field>
                <v-text-field
                        v-model="auth.phone"
                        label="ເບີໂທລະສັບ"
                        outlined
                ></v-text-field>
                <!--                            <v-text-field-->
                <!--                                    v-model="auth.facebookAccount"-->
                <!--                                    label="facebook"-->
                <!--                                    outlined-->
                <!--                            ></v-text-field>-->
                <!--                            <v-text-field-->
                <!--                                    v-model="auth.whatsapp"-->
                <!--                                    label="whatsapp"-->
                <!--                                    outlined-->
                <!--                            ></v-text-field>-->
                <!--                            <v-text-field-->
                <!--                                    v-model="auth.skill"-->
                <!--                                    label="ຄວາມສາມາດ"-->
                <!--                                    outlined-->
                <!--                            ></v-text-field>-->
                <!--                            <v-text-field-->
                <!--                                    v-model="auth.history"-->
                <!--                                    label="ປະຫວັດການເຮັດວຽກ"-->
                <!--                                    outlined-->
                <!--                            ></v-text-field>-->
                <v-btn type="submit" class="success"> ອັບເດດໂປຣຟຣາຍ </v-btn>
            </v-form>
        </v-flex>
        <br>
    </v-card>
</template>

<script>
    import { mapActions } from 'vuex'
    export default {
        props: [
            'auth'
        ],
        data: () => ({
            base64: '',
            file: '',
        }),
        methods: {
            ...mapActions([
                'fetchAuth'
            ]),
            imgUpload(e) {
                const file = e.target.files[0]
                const reader = new FileReader() // Only read base64 not FormData
                if(file['size'] < 2000000) {
                    reader.onloadend = e => {
                        this.base64 = e.target.result
                    }
                    reader.readAsDataURL(file) // Base64 Reader
                    this.file = file // FormData Image
                }else {
                    this.$swal(
                        'Oops...',
                        'You are upload a large file',
                        'error'
                    )
                }
            },
            async updateProfile () {
                try {
                    const user = this.auth
                    const userdata = new FormData()
                    for(let key in user) {
                        userdata.append(key, user[key])
                    }
                    userdata.append('image', this.file)
                    await this.$axios.put('profile/update',  userdata )
                    await this.fetchAuth()
                    this.$router.push({ name: 'Dashboard'})
                    this.$swal({
                        title: 'ອັບເດດໂປຣໄຟຣແລ້ວ',
                        type: 'success',
                        toast: true,
                        position: 'top',
                        showConfirmButton: false,
                        timer: 2000,
                    })
                } catch (e) {
                    throw new Error(e)
                }
            },
        }
    }
</script>


